//Colors
$dark-background: #11284b;
$h2-color: #1a77cd;
//Services
.services-dark {
    background-color: $dark-background;
    h2 {
        color: #1a77cd;
        text-align: center;
        letter-spacing: normal;
        line-height: 64px;
        font-size: 36px;
    }
    span {
        letter-spacing: 1.14px;
        line-height: 22px;
    }
    p {
        color: #f5f5f5;
        line-height: 25px;
        font-size: 16px;
        font-weight: 300;
        height: 3 * 25px;
        overflow: hidden;
    }
    a {
        color: #1A77CD;
        &:hover {
            text-decoration: none;
            color: lighten(#1A77CD, 20%);
        }
    }
}

@media (min-width: 320px) {
    .services-dark {
        padding: 40px 0 80px;
    }
}

@media (min-width: 768px) {
    .services-dark {
        padding: 78px 0 156px;
    }
}

//Banking and Finance page
@media(min-width: 320px) {
    .bankingAndFinance {
        padding: 50px 0 80px;
    }
}

@media(min-width: 992px) {
    .bankingAndFinance {
        padding: 60px 0 85px;
    }
}